import React from "react";
import './Start.css';

function StartScreen (props) {
    var currentName = props.userName;

    if (currentName === "Ciara Lee") {
        currentName = "Ciara Lee, the cutest person in the whole world!"
    }

    return(<div id="startRoom">
        <h1>Welcome!</h1>
        <p>Signed in as {currentName}</p>
        <h2>Terms of Service</h2>
        <p>
            1. Harassment of any kind will result in a ban.<br/>
            2. Markdown is fully supported<br/>
            3. I don't care if this explodes your computer, that is not my problem, don't sue me.<br/>
            4. Any questions? <a href="mailto:HideyBoiDev@gmail.com">Use this email.</a>
        </p>
    </div>)
}

export default StartScreen;